<template>
  <div class="w-100 d-flex flex-column justify-content-around align-items-center" style="height: 100vh; background: #F7F7F8;">
    <Card style="width: 700px;" class="p-4">
      <template slot="header" class="text-center">
        <h2 class="card-title text-center">Login</h2>
      </template>
      <base-input type="text"
                label="Username/Email"
                v-model="user.authId">
      </base-input>
      <base-input type="password"
                label="Password"
                v-model="user.password">
      </base-input>
      <div v-if="error" class="alert alert-danger text-center" role="alert">
        {{error}}
      </div>
      <div class="text-center">
        <button type="submit" class="btn btn-danger btn-fill w-50" @click.prevent="onLogin">
          Login
        </button>
      </div>
    </Card>
  </div>
</template>

<script>
import Card from '../../components/Cards/Card';
import routes from '../../routes/routes';

export default {
  name: 'Login',
  components: {
    Card
  },
  data() {
    return {
      user: {
        authId: '',
        password: '',
      },
      error: null
    }
  },
  methods: {
    onLogin() {
      this.error = null;
      if (this.user.authId && this.user.password) {
        this.$store.dispatch('auth/onLogin', this.user).then(() => {
          this.$store.dispatch('analytics/getReport');
          this.$store.dispatch('hospitalInfo/getMediVues');
          this.$router.push({ name: routes.admin.dashboard.name });
        }).catch(err => {
          this.error = err;
        });
      } else {
        this.error = 'Information missing to login';
      }
    }
  }
}
</script>