import Vue from 'vue';
import gql from '../graphql';
import router from '../../routes'

import serverRoutes from '../serverRoutes';
import apolloClient from '../apollo';
import routes from '../../routes/routes'

const initState = {
  userId: '',
  userInfo: null
};

const state = JSON.parse(JSON.stringify(initState));

const mutations = {
  LOGOUT_USER(state) {
    localStorage.removeItem('user-token');
    state.userId = '';
    state.userInfo = null;
  },
  UPDATE_USER_ID(state) {
    state.userId = localStorage.getItem('user-token'); 
  },
  UPDATE_USER_INFO(state, profile) {
    state.userInfo = JSON.parse(JSON.stringify(profile));
  },
};

const actions = {
  onLogin({ commit, state, dispatch }, user) {
    return new Promise(async (resolve, reject) =>{
      commit('LOGOUT_USER');
      const authRes = await Vue.http.post(serverRoutes.auth.admin.login, { ...user }).catch(err => {
        reject(err.body);
      });

      if (authRes) {
        localStorage.setItem('user-token', authRes.body.admin.id);
        commit('UPDATE_USER_ID');

        dispatch('fetchProfileById', state.userId).then(() => {
          resolve();
        }).catch(err => {
          commit('LOGOUT_USER');
          reject(err);
        });
      }
    });
  },
  fetchProfileById({ commit, dispatch }, adminId) {
    return new Promise(async (resolve, reject) => {
      if (!adminId) {
        reject('must pass an id to fetch the admin');
      }
      const {
        data: {
          getAdminById: {__typename, hospitalId, ...profile}
        },
      } = await apolloClient.query({
        query: gql.getAdminById,
        variables: { adminId },
      }).catch(err => {
        reject(err);
      });

      commit('UPDATE_USER_INFO', profile);
      dispatch('hospitalInfo/getHospitalInfo', hospitalId, {root:true}).then(() => {
        resolve();
      }).catch(err => {
        reject(err);
      });
    })
  },
  logoutUser({ commit }) {
    commit('LOGOUT_USER');
    commit('hospitalInfo/CLEAR_INFO', null, {root:true});
    commit('analytics/CLEAR_ANALYTICS', null, {root:true});
    commit('playlist/CLEAR_PLAYLISTS', null, {root:true});
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};